import React, { useEffect, useRef } from "react";
import styled from "styled-components";
import videojs from "video.js";
// Styles
import "video.js/dist/video-js.css";

import { IVideo } from "./Video";

const Container = styled.video`
    position: absolute;
    bottom: 50px;
    right: 50px;
    width: 240px;
    height: 300px;
`;

export const Video: React.FunctionComponent<IVideo.IProps> = props => {
    const videoRef = useRef(null);
    const [requireRedraw, setRequireRedraw] = useState(false);

    useEffect(() => {
        const videoNode = videoRef.current;
        let player: videojs.Player;
        if (!requireRedraw) {
            player = videojs(
                videoNode.current,
                {
                    autoplay: true,
                    controls: true,
                    sources: [
                        {
                            src: props.videoUrl,
                            type: "video/mp4",
                        },
                    ],
                },
                function onPlayerReady() {
                    console.log("onPlayerReady");
                }
            );
        }
    }, [props.videoUrl]);

    return (
        <div data-vjs-player>
            <Container ref={videoRef} className="video-js">
                <div className="c-player__controls">
                    <button>Play</button>
                    <button>Pause</button>
                </div>
            </Container>
        </div>
    );
};
